<template>
  <div class="contains">
    <!-- <iframe allow="autoplay" :src="music.url" style="display: none;"></iframe> -->
    <button @click="playMusic" style="display: none;">播放</button>
    <audio ref="audio" :src="music.url" @ended="end" controls="controls" style="display: none;"></audio>
    <div :class="`images img${index}`" v-for="(item,index) in imageList"><img :src="item.url"></div>
  </div>
</template>

<script>
  export default {
    mounted() {
      this.playMusic();
      // let button = document.querySelector("button")
      // button.click();
    },
    data() {
      return {
        index: 0,
        imageList: [{
            url: require('@/assets/images/000.jpg')
          },
          {
            url: require('@/assets/images/001.jpg')
          },
          {
            url: require('@/assets/images/002.jpg')
          },
          {
            url: require('@/assets/images/003.jpg')
          },
          {
            url: require('@/assets/images/004.jpg')
          },
          {
            url: require('@/assets/images/005.jpg')
          },
        ],
        music: {
          url: require('@/assets/music/1.mp3')
        },
        musicList: [{
          url: require('@/assets/music/1.mp3')
        }, {
          url: require('@/assets/music/1.mp3')
        }, {
          url: require('@/assets/music/1.mp3')
        }, {
          url: require('@/assets/music/1.mp3')
        }],
        sources: [{
            url: require('@/assets/images/000.jpg')
          },
          {
            url: require('@/assets/images/001.jpg')
          },
          {
            url: require('@/assets/images/002.jpg')
          },
          {
            url: require('@/assets/images/003.jpg')
          },
          {
            url: require('@/assets/images/004.jpg')
          },
          {
            url: require('@/assets/images/005.jpg')
          },
          {
            url: require('@/assets/images/006.jpg')
          },
          {
            url: require('@/assets/images/007.jpg')
          },
          {
            url: require('@/assets/images/008.jpg')
          },
          {
            url: require('@/assets/images/009.jpg')
          },
          {
            url: require('@/assets/images/010.jpg')
          },
          {
            url: require('@/assets/images/011.jpg')
          },
          {
            url: require('@/assets/images/012.jpg')
          },
          {
            url: require('@/assets/images/013.jpg')
          },
          {
            url: require('@/assets/images/014.jpg')
          },
          {
            url: require('@/assets/images/015.jpg')
          },
          {
            url: require('@/assets/images/016.jpg')
          },
          {
            url: require('@/assets/images/017.jpg')
          },
          {
            url: require('@/assets/images/018.jpg')
          },
          {
            url: require('@/assets/images/019.jpg')
          },
          {
            url: require('@/assets/images/020.jpg')
          },
          {
            url: require('@/assets/images/021.jpg')
          },
          {
            url: require('@/assets/images/022.jpg')
          },
          {
            url: require('@/assets/images/023.jpg')
          },
        ],
        pray: [{
          name: '',
          relation: '',
          word: ''
        }]
      }
    },
    methods: {
      playMusic() {
       this.$refs.audio.play();
        //this.$refs.audio.play()
      },
      end() {
        if (this.index < 3) {
          this.index++;
        } else {
          this.index = 0;
        }
        this.imageList = this.sources.slice(this.index * 6, (this.index + 1) * 6)
        this.music = this.musicList[this.index];
        this.playMusic();
      }
    }
  }
</script>

<style scoped>
  .contains {
    /*滚动容器*/
    width: 100%;
    position: relative;
    transform-style: preserve-3d;
    height: 100vh;
    transform: rotate3d(45deg);
    animation: rotate3D 30s linear infinite;
  }

  .images {
    width: 40vw;
    height: 100vh;
    /*transform:translateY(200px);*/
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
  }

  .contains img {
    width: 40vw;
    height: 100vh;
  }

  .img0 {
    transform: rotateY(0deg) translateX(25vw) translateZ(300px);
  }

  .img1 {
    transform: rotateY(60deg) translateX(25vw) translateZ(300px);
  }

  .img2 {
    transform: rotateY(120deg) translateX(25vw) translateZ(300px);
  }

  .img3 {
    transform: rotateY(180deg) translateX(25vw) translateZ(300px);
  }

  .img4 {
    transform: rotateY(240deg) translateX(25vw) translateZ(300px);
  }

  .img5 {
    transform: rotateY(300deg) translateX(25vw)translateZ(300px);
  }
  @keyframes rotate3D {
    0% {
      transform: rotateY(0deg);
    }

    16.7% {
      transform: rotateY(60deg);
    }

    33.3% {
      transform: rotateY(120deg);
    }

    50.1% {
      transform: rotateY(180deg);
    }

    66.7% {
      transform: rotateY(240deg);
    }

    83.4% {
      transform: rotateY(300deg);
    }

    100% {
      transform: rotateY(360deg);
    }

  }
</style>



